*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Protest Strike", sans-serif;
    font-weight: 400;
}
input, button {
    outline: none;
    border: none;
}
body, #root, .app {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
}

.intro {
    width: 100%;
    height: 100%;
    background: linear-gradient(230deg, #F9F871, #FF9671, #FF6F91, #D65DB1, #845EC2);
    display: flex;
    place-content: center;
    flex-direction: column;
    text-align: center;
    .quote {
        font-size: 2rem;
        color: #fff;
        max-width: 65%;
        text-align: center;
        margin-inline: auto;
        text-shadow: 0px 4px #d9d9d994;
    }
    .input_holder {
        width: 100%;
        input, button {
            font-size: 1.05rem;
            color: #fff;
            height: 3rem;
            transition: .5s;
        }
        input {
            width: 30%;
            background: transparent;
            margin-top: 5rem;
            border: 2px solid;
            border-radius: 8px;
            padding: .45rem .55rem .45rem 0.875rem;
            &::placeholder {
                color: #E4E4E4;
            }
        }
        button {
            margin-left: .65rem;
            padding-inline: 2.5rem;
            border-radius: 8px;
            background-color: #8F5EBF;
            cursor: pointer;
            &:hover {
                background-color: #996fc4;
            }
            &:active {
                transform: scale(.95);
            }
        }
    }
}
.tasks_Section {
    min-height: 100vh;
    padding-block: 8rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .tasks_Container {
        .task {
            position: relative;
            background: linear-gradient(90deg, #845EC2, #2C73D2, #0081CF, #0089BA, #008E9B);
            height: 45px;
            width: 750px;
            border-radius: 10px;
            color: #fff;
            display: flex;
            align-items: center;
            padding-left: 1.625rem;
            box-shadow: 4px 4px 6px #7a60c3a6;
            margin-bottom: 1.25rem;
            user-select: none;
            &:hover img {
                opacity: 1;
            }
            .task_title {
                font-size: 1.125rem;
                letter-spacing: 2px;
            }
            .number, .task_done, & > img, .menu {
                position: absolute;
            }
            .number {
                left: -10%;
                color: #6F6F6F;
                font-size: 2rem;
            }
            .task_done {
                bottom: 15%;
                right: 5%;
                font-size: 0.75rem;
                color: #e7e7e7;
            }
            & > img {
                width: 25px;
                right: 1%;
                opacity: 0;
                transition: .5s;
                cursor: pointer;
                &:active {
                    transform: scale(.9);
                }
            }
            .menu {
                right: -15%;
                top: 1%;
                background: linear-gradient(90deg, #845EC2, #2C73D2, #0081CF, #0089BA, #008E9B);
                padding: 0.625rem;
                border-radius: 5px;
                font-size: 0.875rem;
                overflow: hidden;
                transition: .5s;
                .check_task {
                    margin-bottom: .625rem;
                    color: #06bc42;
                }
                .delete_task {
                    color: #ff0000;
                }
                img {
                    height: 15px;
                    margin-left: 8px;
                }
                div {
                    display: flex;
                    align-items: center;
                    cursor: pointer;
                    transition: .5s;
                    &:active {
                        transform: scale(.95)
                    }
                }
                &.hide {
                    opacity: 0;
                }
            }
        }
    }
}